import './css/App.css';
import Clappr from 'clappr'

class Zot {

  constructor(media, elem, options){

    console.log("Lib constructor called");
    this._media = media
    this._elem = elem
    this._options = options



    if(this._media === undefined)
      throw new TypeError('Missing parameter: media')

    if(this._elem === undefined)
      throw new TypeError('Missing parameter: elem')

    if(this._options === undefined)
      throw new TypeError('Missing parameter: options')

    this.initMedia();


  }


  initMedia(){
    switch(this._media){

      case "video":

        //Check video requirements
        if(this._options.source === undefined)
          throw new TypeError('Missing parameter: options.source')

        this._source = this._options.source
        this._id = this._elem.id;
        this._player = new Clappr.Player({source: this._source, parentId: `#${this._id}`});
      break
      default:
      break
    }
  }

  get media(){
    return this._media
  }

  set media(media){
    this._media = media
  }
}

export default Zot;
